.app__media_container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.app__media_container button {
    position: absolute;
    top: 45%;
    z-index: 1;

    border: none;
    outline: none;
    background: rgba(0, 0, 0, 0.418);
    font-size: 20px;
    width: 25px;
    height: 100px;
    color: rgb(160, 160, 160);
}

.button_left {
    left: 0;
}

.button_right {
    right: 0;
}

.app__media_container-carousel {
    width: 95%;
}

.app__media_container button:hover {
    color: var(--color-light-grey);
}


